import TimeUtility from "../utilities/time_utility";
import {AppConfiguration} from "../app_configuration";
import {RuntimeEnvironment} from "../utilities/runtime_environment";


export default class GenericLatencyCollector {
  constructor(url, interval, interval_cb, done_cb, count) {
    this.url = url;
    this.interval = interval;
    this.interval_cb = interval_cb;
    this.done_cb = done_cb;
    this.count = count;
    this.cancelled = false;
  }

  async _get_ttfb() {
    let start = Date.now();
    let res = await fetch(this.url, {
      headers: {
        'Cache-Control': 'no-cache'
      }
    });
    let diff = Date.now() - start;
    await res.text();
    return (RuntimeEnvironment.is_mobile) ? diff : TimeUtility.get_ttfb_for(this.url);
  }

  async start() {
    let intervals = [];
    let i = 0;
    while (i < this.count) {
      const ttfb = await this._get_ttfb();
      if (this.cancelled) {
        return ;
      }
      if (ttfb < AppConfiguration.IGNORE_TTFB_BELOW) {
        continue
      }
      i++;
      intervals.push(ttfb);
      this.interval_cb(ttfb);
      await TimeUtility.sleep(this.interval)
    }
    if (this.done_cb) {
      this.done_cb();
    }
    console.log('intervals', intervals.length);
  }

  cancel() {
    this.cancelled = true;
  }
}
