import * as d3 from 'd3';
import React, {Component} from "react";
import {Button, Row, Col, Navbar} from "react-bootstrap";
import '../components/component_styles.scss';
import LatencyStageReport from "../components/latency_stage_report";
import LatencyResultsReport from "../components/latency_results_report";
import {AppConfiguration} from "../app_configuration";
import LatencyMeasurementStage from "../stages/latency_measurement_stage";
import DownlinkMeasurementStage from "../stages/downlink_measurement_stage";
import UplinkMeasurementStage from "../stages/uplink_measurement_stage";
import StatisticsUtilities from "../utilities/statistics_utilities";
import {DEBUG_STATE} from "../test/debug_state";
import BandwidthReportComponent from "../components/bandwidth_report_component";
import CardTitleComponent from "../components/card_title_component";
import ReportGenerator from "../tools/report_generator";
import TestResultsManager from "../tools/test_results_manager";
import ShareResultsComponent from "../components/share_results_component";


export default class BufferBloatTestComponent extends Component {
  static TEST_NOT_FOUND = "not found";
  BOX_PLOT_SCALE_DEFAULT = [0, 5];

  _get_initial_state() {
    return {
      test_running: false,
      test_title: 'Bufferbloat Test',
      current_stage_idx: -1,
      stages: {},
      test_id: null,
      report: null,
      box_plot_scale: this.BOX_PLOT_SCALE_DEFAULT,
      download_speed: null,
      upload_speed: null,
    };
  }

  constructor(props) {
    super(props);
    this.stages_configuration = [
      {
        name: "unloaded",
        title: "Unloaded",
        page_title: "Measuring Unloaded Latency"
      },
      {
        name: "down",
        title: " Saturated",
        page_title: "Measuring Latency With Downlink Saturated"
      },
      {
        name: "up",
        title: " Saturated",
        page_title: "Measuring Latency With Uplink Saturated"
      }
    ];
    this.stage_context = null;
    this.state = this._get_initial_state();
    performance.setResourceTimingBufferSize(AppConfiguration.RESOURCE_TIMING_BUFFER_SIZE);
    this.stages = [
      new LatencyMeasurementStage(
        this.add_latency_to_current_stage.bind(this),
        this.advance_stage.bind(this)
      ),
      new DownlinkMeasurementStage(
        this.add_latency_to_current_stage.bind(this),
        this.downlink_measurement.bind(this),
        this.advance_stage.bind(this)
      ),
      new UplinkMeasurementStage(
        this.add_latency_to_current_stage.bind(this),
        this.uplink_measurement.bind(this),
        this.advance_stage.bind(this)
      )
    ];
  }

  downlink_measurement(bandwidth) {
    console.log("downlink bandwidth", bandwidth);
    this.setState({
      download_speed: bandwidth
    });
    this.stage_context = {
      downlink_speed: bandwidth
    };
  }

  uplink_measurement(bandwidth) {
    console.log("uplink bandwidth", bandwidth);
    this.setState({
      upload_speed: bandwidth
    });
  }

  async test_again() {
    await this.update_current_state({
      ...this._get_initial_state()
    });
    await this.start_test();
  }

  async debug() {
    //await this.start_test();
    await this.setState({
      ...DEBUG_STATE[6],
      current_stage_idx: 3
    });
    await this.advance_stage();
  }

  async advance_stage() {
    let curr_idx = this.state.current_stage_idx + 1;
    if (curr_idx >= this.stages.length) {
      let stages_flattened = this.state.stages;
      stages_flattened = Object.keys(stages_flattened).map(k => stages_flattened[k]);
      await this.setState({
        report: ReportGenerator.generate_report({
          stages: stages_flattened,
          download_speed: this.state.download_speed,
          upload_speed: this.state.upload_speed,
          current_stage_idx: curr_idx,
        })
      });
      let test_id = await TestResultsManager.post_results(this.state);
      await this.setState({
        test_id: test_id
      });
      return;
    }
    await this.setState({
      current_stage_idx: curr_idx,
      stages: {
        ...this.state.stages,
        [curr_idx]: {
          configuration: this.stages_configuration[curr_idx],
          data: []
        }
      },
      test_title: `${curr_idx + 1}/${this.stages_configuration.length} ${this.stages_configuration[curr_idx].page_title}`
    });
    this.stages[curr_idx].run(this.stage_context);
  }

  async start_test() {
    await this.setState({
      test_running: true,
    });
    this.advance_stage();
  }

  adjust_scale(latency) {
    if (latency > AppConfiguration.MAX_LATENCY_PLOTTED) {
      return;
    }
    if (latency > this.state.box_plot_scale[1]) {
      this.setState({
        box_plot_scale: [0, latency]
      })
    }
  }

  async add_latency_to_current_stage(latency) {
    this.adjust_scale(latency);
    let curr_idx = this.state.current_stage_idx,
      current_stage = this.state.stages[curr_idx],
      current_data = current_stage.data || [],
      new_data = [...current_data, {value: latency}];
    await this.setState({
      stages: {
        ...this.state.stages,
        [curr_idx]: {
          ...current_stage,
          data: new_data,
          stats: {
            ...this.calculate_stats(new_data)
          }
        }
      }
    })
  }

  calculate_stats(data) {
    let sorted = [...data.map(d => d.value)].sort(d3.ascending);
    let [q1, q3, median, min, max, std] = StatisticsUtilities
      .calculate_important_values(sorted)
      .map(d => parseFloat(d.toFixed(1)));
    return {
      min: min,
      max: max,
      q1: q1,
      q3: q3,
      median: median,
      jitter: std
    };
  }

  async update_current_state(state) {
    this.test_result = state;
    this.setState({
      ...state
    });
  }

  render() {
    const test_view = (
      <div className="card-body">
        <LatencyResultsReport
          stage_data={this.state.stages}
        />
        {
          Object.keys(this.state.stages).map((i) => {
            let stage = this.state.stages[i];
            return (
              <div key={stage.configuration.name}>
                <LatencyStageReport
                  box_plot_scale={this.state.box_plot_scale}
                  title={stage.configuration.title}
                  key={stage.configuration.name}
                  data={stage.data}
                  stats={stage.stats}
                  spacer_before={parseInt(i) !== 0}
                />
              </div>
            )
          })
        }
        <BandwidthReportComponent
          download_speed={this.state.download_speed}
          upload_speed={this.state.upload_speed}
        />
      </div>
    );

    const get_view = () =>  {
      if (this.props.running_new_test) {
        if (this.state.test_running) {
          return test_view;
        } else {
          return (
            <div className="card-body WBBStartRunningTheTest">
              <div>
                Click below to start running the test.
              </div>
              <Button bsStyle="primary" onClick={this.start_test.bind(this)}>Start Test</Button>
            </div>
          )
        }
      }
      if (this.test_result === BufferBloatTestComponent.TEST_NOT_FOUND) {
        return (
          <div className="card-body">
            <div>
              Test was not found.
            </div>
          </div>
        )
      }
      if (this.test_result) {
        return test_view;
      } else {
        return (
          <div className="card-body">
            <div>
              Loading.
            </div>
          </div>
        )
      }
    };

    return (
      <>
        <div className="container WBBContainer">
          <div className="WBBCard WBBMainTestCard">
            <div className="card-header">
              <CardTitleComponent
                title={this.state.test_title}
                report={this.state.report}
              />
            </div>
            {
              get_view()
            }
            <ShareResultsComponent
              test_again={this.test_again.bind(this)}
              test_id={this.state.test_id}
            />
          </div>
        </div>
      </>
    );
  }
}
