import React, {Component} from 'react';
import {FormGroup, Row, Col, ControlLabel, FormControl, Form, Button} from "react-bootstrap";
import TestResultsManager from "../tools/test_results_manager";
import "./share_results_component.scss"
import {AppConfiguration} from "../app_configuration";


export default class ShareResultsComponent extends Component {
  copy_to_clipboard(e) {
    document.querySelector('#shareResults').select();
    document.execCommand('copy');
  }

  render() {
    const create_url = (test_id) => `${window.location.origin}?${AppConfiguration.TEST_ID_QUERY}=${test_id}`;
    let test_id = this.props.test_id;
    return (
      <div className={`WBBFooter ${test_id ? "WBBResultsLink" : ""}`}>
        {(test_id) ?
          (<>
            <Row>
              <Col xs={12} className={"WBBNoPadding"}>
                <Form inline>
                  <FormGroup controlId="shareResults">
                    <ControlLabel>
                      Share<span> Your Results</span>:
                    </ControlLabel>
                    <FormControl type="text" value={create_url(this.props.test_id)}/>
                    <Button className={"copy-button"} onClick={this.copy_to_clipboard.bind(this)}>
                      Copy
                      <img src="copy.svg" alt="copy-icon"/>
                    </Button>
                    <div className={"test-again"}>
                      <Button bsStyle="primary" onClick={this.props.test_again}>
                        Test Again
                      </Button>
                    </div>
                  </FormGroup>
                </Form>
              </Col>
            </Row>
          </>): (<></>)}
      </div>
    )
  }
}