import Stage from "./stage";
import LatencyCollectorProvider from "../tools/latency_collector_provider";
import GuessBasedUploader from "../tools/guess_based_uploader";
import {AppConfiguration} from "../app_configuration";
import UploadBasedOnGuess from "../tools/upload_based_on_guess";


export default class UplinkMeasurementStage extends Stage {
  constructor(data_callback, bandwidth_callback, done_callback) {
    super();
    this.data_callback = data_callback;
    this.bandwidth_callback = bandwidth_callback;
    this.done_callback = done_callback;
  }

  async run(context) {
    const downlink_speed = context.downlink_speed;
    const MEASURE_LATENCY_FOREVER = 1e5;
    super.run();
    const collector = LatencyCollectorProvider.provide(this.data_callback, null, MEASURE_LATENCY_FOREVER);
    collector.start();
    let uploader = new UploadBasedOnGuess();
    let upload_measurements = await uploader.upload_based_on_downlink_guess(
      downlink_speed,
      AppConfiguration.UPLOAD_DURATION
    );
    collector.cancel();
    this.bandwidth_callback(Math.max(...upload_measurements));
    this.done_callback();
  }
}