export default class ReportGenerator {
  static generate_report(test_data) {
    const LARGER_THAN = "LARGER_THAN",
      SMALLER_THAN = "SMALLER_THAN";
    const categories = [
      // sources:
      // 1. https://broadbandnow.com/guides/how-much-internet-speed-do-i-need
      // 2. https://www.phone.com/much-bandwidth-need-voip/
      // 3. https://www.lifewire.com/what-is-latency-and-how-it-can-be-reduced-3426314#:~:text=Latency%20is%20measured%20in%20milliseconds,higher%2C%20latency%20becomes%20completely%20unacceptable.
      // 4. https://www.highspeedinternet.com/resources/how-much-speed-do-i-need-for-online-gaming
      {
        name: "Web Browsing",
        criteria: {
          downlink: [LARGER_THAN, 1],
          uplink: [LARGER_THAN, 1],
          latency: null,
          latency_with_jitter: null,
        }
      },
      {
        name: "Audio Calls",
        criteria: {
          downlink: [LARGER_THAN, 3],
          uplink: [LARGER_THAN, 3],
          latency: [SMALLER_THAN, 150],
          latency_with_jitter: [SMALLER_THAN, 200],
        }
      },
      {
        name: "4K Video Streaming",
        criteria: {
          downlink: [LARGER_THAN, 40],
          uplink: null,
          latency: null,
          latency_with_jitter: null,
        }
      },
      {
        name: "Video Conferencing",
        criteria: {
          downlink: [LARGER_THAN, 2.5],
          uplink: [LARGER_THAN, 2.5],
          latency: [SMALLER_THAN, 150],
          latency_with_jitter: [SMALLER_THAN, 200],
        }
      },
      {
        name: "Online Gaming",
        criteria: {
          downlink: [LARGER_THAN, 3],
          uplink: [LARGER_THAN, 0.5],
          latency: [SMALLER_THAN, 100],
          latency_with_jitter: [SMALLER_THAN, 150],
        }
      }
    ];

    let [downlink, uplink] = [test_data.download_speed/1e6, test_data.upload_speed/1e6],
      stages_data = Object.keys(test_data.stages).map(k => test_data.stages[k]),
      latency = Math.max(...stages_data.map(stage_data => stage_data.stats.median)),
      latency_with_jitter = Math.max(
      ...stages_data.map(
        stage_data => stage_data.stats.median + stage_data.stats.jitter
      )
    );

    const gradings =  [
      {
        until: 5,
        grade: "A+"
      },
      {
        until: 30,
        grade: "A"
      },
      {
        until: 60,
        grade: "B"
      },
      {
        until: 200,
        grade: "C"
      },
      {
        until: 400,
        grade: "D"
      },
      {
        until: Infinity ,
        grade: "F"
      }
    ];

    const latencies = test_data.stages.map(stage_data => stage_data.stats.median);
    let  downlink_bb = (latencies[1] - latencies[0]),
      uplink_bb = latencies[2] - latencies[0];
    downlink_bb = (downlink_bb > 0) ? downlink_bb : 0;
    uplink_bb = (downlink_bb > 0) ? uplink_bb : 0;
    const combined_bb = (downlink_bb + uplink_bb)/2;
    const measurements = {
      downlink: downlink,
      uplink: uplink,
      latency: latency,
      latency_with_jitter: latency_with_jitter,
    };

    function grade(combined_bb) {
      for (let i in gradings) {
        let grading = gradings[i];
        if (combined_bb < grading.until) {
          return grading.grade;
        }
      }
      return gradings.slice(-1).pop().grade;
    }

    return {
      categories: categories.map(category => {
        const criteria = category.criteria,
          criteria_names = Object.keys(criteria);
        const report = {
          name: category.name,
          criteria: criteria_names.reduce((dict, c_name) => {
            if (criteria[c_name] === null) {
              dict[c_name] = true;
              return dict;
            }
            if (criteria[c_name][0] === LARGER_THAN) {
              dict[c_name] = measurements[c_name] > criteria[c_name][1];
              return dict;
            }
            dict[c_name] = measurements[c_name] < criteria[c_name][1];
            return dict;
          }, {})
        };
        return report;

      }),
      grade: grade(combined_bb)
    }
  }
}