import React, {Component} from 'react';
import {Row, Col} from 'react-bootstrap';
import LatencyStatsComponent from "./latency_stats_component";
import './latency_stage_report.scss';
import LatencyBoxPlot from "./latency_box_plot";


export default class LatencyStageReport extends Component {
  constructor(props) {
    super(props);
    [this.width, this.height] = this.get_size_based_on_window_size();
  }

  get_size_based_on_window_size() {
    const BOOTSTRAP_SM = 768;
    const BOOTSTRAP_MD = 992;
    let window_size = window.visualViewport.width;
    if (window_size < BOOTSTRAP_SM) {
      return [window_size * 8/12, 70];
    }
    if (window_size < BOOTSTRAP_MD) {
      return [window_size * 5/12, 70];
    }
    return [null, null];
  }

  render() {
    return (
      <>
        <div className={"WBBLatencyStageReport"}>
          <Row>
            {
              (this.props.spacer_before) ?
                (<Col sm={12} className={"spacer"}>
                </Col>)
                : (<></>)
            }
            <Col sm={2} md={2} xs={3} className={"title"}>
              <h5 style={{marginTop: 15}}>{this.props.title}</h5>
            </Col>
            <Col sm={7} md={7} xs={9} className={"WBBBoxPlotContainer"}>
              <LatencyBoxPlot
                width={this.width}
                height={this.height}
                data={this.props.data}
                scale={this.props.box_plot_scale}
                tooltip_html={"latency: ${d.value}</br>timestamp: ${d.time}"}
              />
            </Col>
            <Col sm={3} md={3} xs={12} className={"WBBStageLatencyStats"} >
              <LatencyStatsComponent
                data={this.props.data}
                {...this.props.stats}
              />
            </Col>
          </Row>
        </div>
      </>
    );
  }
}
