import React, {Component} from 'react';
import {Row, Col} from "react-bootstrap";
import {StylesConfiguration} from "../styles_configuration";


export default class BufferbloatCallouts extends Component {
  constructor(props) {
    super(props);
    this.median = 0;
  }

  report_bufferbloat(stage, i) {
    if (!stage.stats) {
      return ;
    }
    const BAD_MEASUREMENT_THRESHOLD = 50;
    let measurement = 0, status_class="";
    if (i === 0) {
      measurement = stage.stats.median;
      this.median = measurement;
    } else {
      measurement = stage.stats.median - this.median;
      measurement = (measurement >= 0) ? measurement : 0;
      measurement = `+${measurement.toFixed(2)}`;
      status_class = (measurement < BAD_MEASUREMENT_THRESHOLD) ? "good" : "bad";
    }
    measurement += " ms";
    return (
      <div className={`latency ${status_class}`}>
        {
          measurement
        }
      </div>
    )
  }


  render() {
    return (
      <>
        <Row style={{textAlign: 'center'}}>
          {Object.keys(this.props.stage_data).map((k, i) => {
            const stage = this.props.stage_data[k];
            const length = Object.keys(this.props.stage_data).length;
            return (
              <Col
                className={"callout"}
                key={k}
                xs={12/length}
                style={ (i !== 0) ? {borderLeft: StylesConfiguration.DIVIDER_BORDERS}: {}}>
                <div className="condition">
                  {stage.configuration.title}
                </div>
                {
                  this.report_bufferbloat(stage, i)
                }
              </Col>
            )
          })}
        </Row>
      </>
    )
  }
}