import TimeUtility from "../utilities/time_utility";
import BandwidthMeasurementUtilities from "../utilities/bandwidth_measurement_utilities";
import {RuntimeEnvironment} from "../utilities/runtime_environment";

export default class UploadBasedOnGuess {
  async upload_based_on_downlink_guess(downlink_speed, duration) {
    const upload_url = (i) => `https://speed.cloudflare.com/__up?uuid=${i}`;
    const [file_size, num_measurements] = this.get_upload_size(downlink_speed, duration);
    console.log("num measurements", num_measurements);
    let measurements = [];
    for (let i in [...Array(num_measurements).keys()]) {
      const url = upload_url(i);
      let res = await fetch(url, {
        method: 'POST',
        body: "0".repeat(file_size),
      });
      await res.text();
      let measurement = this.calculate_performance_for_entry(file_size, url);
      console.log("calculated measurement", measurement);
      measurements.push(measurement);
    }
    console.log("all measurements", measurements);
    return measurements;
  }

  get_upload_size(downlink_speed, duration) {
    const MAX_FILE_SIZE = 1e8;
    let total_upload = duration * downlink_speed / 8;
    let num_measurements = 10;
    let file_size = total_upload/num_measurements;
    if (file_size > MAX_FILE_SIZE) {
      file_size = MAX_FILE_SIZE;
      num_measurements = total_upload/file_size;
    }
    return [file_size, num_measurements];
  }

  calculate_performance_for_entry(size, url) {
    const time_difference = (RuntimeEnvironment.is_mobile) ?
      TimeUtility.get_ttfb_for(url):
      TimeUtility.get_server_timing_for(url);
    return BandwidthMeasurementUtilities.to_bps(size/time_difference);
  }
}
