import TimeUtility from "../utilities/time_utility";
import BandwidthMeasurementUtilities from "../utilities/bandwidth_measurement_utilities";
import DebugTools from "../test/debug_tools";

export default class DownloadMeasurementCollector {
  constructor(measurement_callback, done_callback) {
    this.measurement_callback = measurement_callback || (() => null);
    this.done_callback = done_callback || (() => null);
    this.controller = new AbortController();
  }

  get_http_fetch(size, i) {
    let signal = this.controller.signal;
    //return fetch(`https://speed${i+1}.arshankhanifar.workers.dev/hi/down?bytes=${size}`, {signal});
    return fetch(`https://speed.cloudflare.com/__down?bytes=${size}`, {signal});
  }

  async download_for_n_seconds(n, streams) {
    this.running = true;
    const MAX_DOWNLOAD_SIZE = 1e8;
    setTimeout(() => {this.cancel()}, n * 1000);
    return this.measure_download(MAX_DOWNLOAD_SIZE, streams);
  }

  make_requests(num_streams, size) {
    return Array(num_streams)
      .fill(0)
      .map(_ => { return {} })
      .map((req, i) => {
        req.fetch = () => this.get_http_fetch(size, i);
        return req;
      })
  }

  bandwidth_from_last_n_samples(tracking, n) {
    if (tracking.length < n) {
      return 0;
    }
    let [this_time, this_data] = tracking.slice(-1).pop();
    let [last_time, last_data] = tracking.slice(-n, -n + 1).pop();
    let bw = ((this_data - last_data )/(this_time - last_time) * 8 / 1e3);
    return bw;
  }

  async measure_download(size, streams) {
    let total = 0;
    let bytes = [[Date.now(), total]];
    let measurements = [];
    let self = this;
    let requests = this.make_requests(streams, size)
      .map(async (req, i) => {
        return new Promise(async resolve => {
          let res = null;
          while (this.running) {
            try {
              res = await req.fetch();
            } catch (e) {
              console.log('err1', e);
              return resolve();
            }
            const reader = res.body.getReader();
            try {
              await pump();
            } catch (err) {
              console.log('err2', err);
              resolve();
            }
            function pump() {
              return reader.read().then(({ done, value }) => {
                if (done) {
                  return;
                }
                total += value.length;
                bytes.push([Date.now(), total]);
                measurements.push(self.bandwidth_from_last_n_samples(bytes, 10));
                return pump();
              });
            }
          }
          resolve();
        });
      });
    await Promise.all(requests);
    let rate = BandwidthMeasurementUtilities.collect_good_measurements(bytes);
    this.done_callback(rate);
    return rate;
  }

  cancel() {
    this.running = false;
    this.controller.abort();
  }
}