import StatisticsUtilities from "./statistics_utilities";

export default class BandwidthMeasurementUtilities {
  static get_regression_from_upload_progress(byte_tracker) {
    let first_time = byte_tracker[0][0];
    let formatted = byte_tracker.map(entry => {
      return {
        x: entry[0] - first_time,
        y: entry[1]
      }
    });
    return StatisticsUtilities.get_linear_regression(formatted);
  }

  static collect_good_measurements(bytes_info) {
    const THRESHOLD_NUM_DATA = 50,
      THRESHOLD_ACCEPTANCE_CONFIDENCE = 0.99;
    let i = 1;
    let measured = [];
    const to_bps = BandwidthMeasurementUtilities.to_bps;
    const get_reg = (bytes) => BandwidthMeasurementUtilities.get_regression_from_upload_progress(bytes);
    while (i < bytes_info.length/2) {
      let sliced = bytes_info.slice(i, -i);
      if (sliced.length < THRESHOLD_NUM_DATA) {
        break;
      }
      let reg = get_reg(sliced);
      if (reg.rSquared > THRESHOLD_ACCEPTANCE_CONFIDENCE) {
        measured.push(to_bps(reg.a));
      }
      i+=10;
    }
    if (measured.length === 0) {
      console.warn("Download data too noisy, internet connection might not be stable");
      let reg = get_reg(bytes_info);
      return to_bps(reg.a);
    }
    return Math.max(...measured)
  }

  static to_bps(bw) {
    return bw * 8 * 1e3;
  }
}