import React, {Component} from 'react';
import {Row, Col} from "react-bootstrap";
import {StylesConfiguration} from "../styles_configuration";
import {AppConfiguration} from "../app_configuration";
import "./card_title_component.scss";


export default class CardTitleComponent extends Component {
  get_picture(category) {
    const criteria = Object.keys(category.criteria);
    let met_criteria = criteria.map(criterion => category.criteria[criterion]).reduce((a, b) => a && b);
    let path = AppConfiguration.STATIC_RESOURCES_PATH + ((met_criteria) ? "shield.svg" : "bad.svg");
    return  path;
  }

  render() {
    const fix_bb_link = "https://www.bufferbloat.net/projects/bloat/wiki/What_can_I_do_about_Bufferbloat/";

    return (
      <>
        {
          this.props.report ? (
            <div className={"WBBCardTitle WBBTestingDone"}>
              <Row className={"WBBYourGrade"}>
                <h2>
                  Your Bufferbloat Grade: {this.props.report.grade}
                </h2>
                <p>Read about <a href={fix_bb_link}>how to fix bufferbloat issues.</a></p>
              </Row>
              <Row className={"WBBCategoryContainer"}>
                {this.props.report.categories.map(category => (
                  <Col className={"indicator"} key={category.name}>
                    <img src={this.get_picture(category)} alt="indicator-good"/>
                       {' '}
                       <strong>
                         {category.name}
                       </strong>
                       {' '}
                  </Col>
                ))}
              </Row>
              <a className={"WBBHowToFixSmallScreen"} href={fix_bb_link}>How to fix bufferbloat issues.</a>
            </div>
          ):(
            <div className={"WBBCardTitle"}>
              <h2>{this.props.title}</h2>
            </div>
          )
        }
      </>
    )
  }
}
