import {
  BrowserRouter as Router,
  Link,
  useLocation,
  withRouter
} from "react-router-dom";

import React, {Component} from "react";
import {DEBUG_STATE} from "../test/debug_state";
import {AppConfiguration} from "../app_configuration";
import TestResultsManager from "../tools/test_results_manager";
import TimeUtility from "../utilities/time_utility";
import BufferBloatTestComponent from "../components/buffer_bloat_test_component";


class QueryParamsExtractor extends Component {
  constructor(props) {
    super(props);
    const { match, location, history } = this.props;
    this.buffer_bloat = React.createRef();
    let query = new URLSearchParams(location.search);
    const test_id = query.get(AppConfiguration.TEST_ID_QUERY);
    const running_new_test = (test_id || "").length === 0;
    this.state = {
      test_result: null,
      running_new_test: running_new_test,
      test_id: test_id
    }
  }
  componentDidMount() {
    this.load_state().then(()=>{});
  }

  async load_state() {
    if (this.state.running_new_test) {
      return;
    }
    let state = null;
    try {
      state = await TestResultsManager.get_test_results(this.state.test_id);
    } catch (e) {
      state = BufferBloatTestComponent.TEST_NOT_FOUND;
    }
    this.buffer_bloat.current.update_current_state(state);
  }

  render() {
    return (
      <BufferBloatTestComponent
        ref={this.buffer_bloat}
        running_new_test={this.state.running_new_test}
        test_result={this.state.test_result}
      />
    )
  }
}

const QueryParamsExtractorWithRouter = withRouter(QueryParamsExtractor);

export default class MainPage extends Component {
  render() {
    return (
      <Router>
        <QueryParamsExtractorWithRouter/>
      </Router>
    )
  }
}


