import React, {Component} from 'react';
import {Row, Col} from "react-bootstrap";
import {StylesConfiguration} from "../styles_configuration";
import './bandwidth_report_component.scss';


export default class BandwidthReportComponent extends Component {
  render() {
    return (
      <>
        {
          (this.props.download_speed || this.props.upload_speed) &&
          (
            <Row className="WBBBandwidthReportComponent" style={{marginTop: 15}}>
              <Col xs={0} sm={1} md={2}></Col>
              <Col className="WBBSpeedTestResults" xs={12} sm={4} md={4}>
                <h4>Speed Test Results:</h4>
              </Col>
              <Col className={"WBBSpeedMeasurementPlaceholder"} xs={12} sm={5} md={4}>
                <div>
                  {
                    this.props.download_speed &&
                    (<Col xs={6} className={"WBBDownload"}>
                      <div>
                         Download
                      </div>
                      <div className={"WBBValue"}>
                        {(this.props.download_speed/1e6).toFixed(2)}Mbps
                      </div>
                    </Col>)
                  }
                  {
                    this.props.upload_speed &&
                    (<Col xs={6} className={"WBBUpload"} style={{borderLeft: StylesConfiguration.DIVIDER_BORDERS}}>
                      <div>
                         Upload
                      </div>
                      <div className={"WBBValue"}>
                        {(this.props.upload_speed/1e6).toFixed(2)}Mbps
                      </div>
                    </Col>)
                  }
                </div>
              </Col>
            </Row>
          )
        }
      </>
    )
  }
}
