import React, {Component} from 'react';
import {Row, Col} from 'react-bootstrap';


export default class LatencyStatsComponent extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <>
        <Row style={{fontSize: 13, whiteSpace: 'nowrap'}}>
          <Col sm={6}>
            min: {this.props.min}ms<br/>
            median: {this.props.median}ms<br/>
            max: {this.props.max}ms
          </Col>
          <Col sm={6}>
            25%: {this.props.q1}ms<br/>
            75%: {this.props.q3}ms<br/>
            jitter: {this.props.jitter}ms
          </Col>
        </Row>
      </>
    )
  }
}